.MovieCard {
  position: relative;
  padding: 8px;
  height: 100%;
  color: #0c161e;
  background-color: #fff;
  border-radius: 6px;
  overflow: hidden;
  -webkit-box-shadow: 5px 5px 25px 0px rgba(46, 61, 73, 0.2);
          box-shadow: 5px 5px 25px 0px rgba(46, 61, 73, 0.2);
  -webkit-transition: -webkit-box-shadow 0.3s ease-out;
  transition: -webkit-box-shadow 0.3s ease-out;
  -o-transition: box-shadow 0.3s ease-out;
  transition: box-shadow 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, -webkit-box-shadow 0.3s ease-out;
}

.MovieCard:hover {
  -webkit-box-shadow: 0px 0px 25px 2px rgba(230, 236, 241, 0.4);
          box-shadow: 0px 0px 25px 2px rgba(230, 236, 241, 0.4);
}

.MovieCard__rating {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #e2b100;
  border: 1px solid #dea12f;
  width: 50px;
  height: 35px;
  font-weight: bold;
  line-height: 35px;
  text-align: center;
  border-bottom-right-radius: 35%;
}

.MovieCard__poster {
  display: block;
  width: 100%;
  height: 180px;
  margin-bottom: 10px;
  -o-object-fit: cover;
     object-fit: cover;
}

.MovieCard__btn {
  position: absolute;
  display: block;
  outline: 0;
  border: 0;
  background-color: #e2b100;
  cursor: pointer;
  color: #0c161e;
  font-weight: bold;
  bottom: 8px;
  right: 8px;
  width: 35px;
  height: 35px;
  font-size: 24px;
  line-height: 35px;
  border-radius: 4px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
          box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
}
.MovieCard__btn:hover {
  -webkit-box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
          box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
  background-color: #3f51b5;
  color: #fff;
}



