.MovieList {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex: 0 0 78%;
      flex: 0 0 78%;
}

.MovieList__item {
  width: 25%;
  padding: 20px;
}

.MovieList__msg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 38px;
  line-height: 1.5;
  text-transform: uppercase;
}



